.shop {
	padding: 70px 0 40px 0;
	@media screen and (max-width: 765px) {
		padding: 50px 0 30px 0; }
	@media screen and (max-width: 340px) {
		padding: 30px 0 20px 0; }
	.title {
		margin-top: 10px; }
	&__girl {
			@media screen and (max-width:1180px) {
				width: 280px !important;
				height: 330px; }
			@media screen and (max-width: 500px) {
				width: 250px !important;
				height: 300px; } }

	&__filter-wrapper {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		@media screen and (max-width: 765px) {
			flex-direction: column-reverse; }
		form {
			margin-right: 30px;
			@media screen and (max-width: 765px) {
				margin-bottom: 20px; }
			@media screen and (max-width: 340px) {
				margin-right: 0px;
				align-self: center; } } }
	&__search {
		display: flex;
		flex-wrap: wrap;
		@media screen and (max-width: 400px) {
			flex-direction: column; }
		&-label {
			margin-right: 20px; }
		&-input {
			width: 180px;
			height: 30px;
			box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.25);
			border: none;
			border-radius: 4px;
			font-size: 12px;
			padding: 0 15px;
			&::placeholder {
					text-align: center; } } }
	&__filter {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: flex-start;
		@media screen and (max-width: 765px) {
			margin-bottom: 30px; }

		&-label {
			margin-right: 35px;
			@media screen and (max-width: 765px) {
				margin-right: 20px;
				margin-bottom: 10px; } }
		&-btn {
			width: 70px;
			height: 30px;
			background-color: #fff;
			border: none;
			box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.25);
			font-size: 12px;
			outline: none;
			cursor: pointer;
			transition: all 0.2s;
			&:hover {
				background: #555;
				color: #fff; }
			&:first-child {
					border-radius: 4px 0px 0px 4px; }
			&:last-child {
					border-radius: 0px 4px 4px 0px; }
			&:focus {
					outline: none; } } } }
.reset-all-filter {
	width: 30px; }


