.header {
    display: flex;
    align-items: flex-end;
    padding-top: 30px;
    list-style-type: none;
    padding-left: 0;
    @media screen and (max-width: 430px) {
        flex-wrap: wrap;
        justify-content: center; }
    &__item {
        font-size: 12px;
        margin-right: 40px;
        font-weight: normal;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        @media screen and (max-width: 430px) {
            margin-bottom: 20px; }
        &:first-child {
            transform: translateY(3px); }
        a {
            transition: color 0.2s;
            color: #fff;
            &:visited {
                text-decoration: none;
                color: #fff; }
            &:hover {
                text-decoration: none;
                color: #aaa; } } } }


.preview {
    max-height: 640px;
    background: url('../../img/Main_bg.jpg') center center no-repeat #777;
    background-size: cover;
    padding-bottom: 40px;
    a {
        text-decoration: none; }
    .title-big {
        margin-top: 110px;
        @media screen and (max-width: 500px) {
            margin-top: 30px;
            font-size: 35px; }
        @media screen and (max-width: 400px) {
            margin-top: 0px;
            font-size: 25px; } }
    &__subtitle {
        color: #ffffff;
        font-weight: bold;
        font-size: 40px;
        margin-top: 110px;
        margin-bottom: 0;
        text-align: center;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        @media screen and (max-width: 500px) {
            font-size: 30px; } }
    &__subtitle {
        font-size: 24px;
        margin-top: 20px;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); }
    &__btn {
        display: block;
        width: 120px;
        height: 30px;
        margin: 0 auto;
        margin-top: 18px;
        border: 1px solid #fff;
        border-radius: 4px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.05);
        color: #fff;
        text-align: center;
        font-weight: bold;
        line-height: 28px;
        font-size: 14px;
        transition: all 0.2s;
        &:hover {
            background: rgba(255, 255, 255, 0.7);
            color: #000; }
        &:active {
            background: rgba(255, 255, 255, 0.3);
            color: #000; } } }
