footer {
    padding: 20px 0;
    @media screen and (max-width: 410px) {
        padding: 10px 0; } }
.footer {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    margin-top: 30px;
    list-style-type: none;
    @media screen and (max-width: 410px) {
        flex-wrap: wrap;
        margin-top: 0; }
    &__item {
        font-size: 12px;
        margin-right: 30px;
        font-weight: normal;
        @media screen and (max-width: 410px) {
            margin-top: 20px; }
        a {
            color: #000;
            &:visited {
                text-decoration: none;
                color: #000; }
            &:hover {
                text-decoration: none;
                color: #666; } } } }
