
.error {
	&__wrapper {
		display: block;
		margin: 70px auto 10px auto;
		padding: 30px 50px;
		text-align: center;
		border-radius: 5px;
		width: 600px;
		box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.25);
		background: #efffff; }
	&__img {
		width: 200px;
		height: 200px;
		@media screen and (max-width: 380px) {
			width: 150px;
			height: 150px; } }
	&__text {
		font-size: 20px;
		margin-top: 30px; } }
