.contact {
	&__title {
		margin-top: 60px; }
	&__form {
		display: flex;
		flex-direction: column;
		justify-content: center;
		margin-top: 40px;
		margin-bottom: 30px; }
	&__row {
		text-align: center;
		margin-bottom: 34px; }
	&__obligatory-field {
		color: #FF0000; }
	&__search-label {
		margin-right: 130px;
		font-size: 18px; }
	&__search-input {
		width: 220px;
		height: 30px;
		background: #FFFFFF;
		border: 1px solid rgba(0, 0, 0, 0.49);
		box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.25);
		border-radius: 4px;
		padding-left: 5px; }
	&__message {
		&-label {
			font-size: 18px;
			margin-bottom: 30px; }
		&-input {
			width: 350px;
			height: 120px;
			font-size: 14px;
			background: #FFFFFF;
			border: 1px solid rgba(0, 0, 0, 0.49);
			box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.25);
			border-radius: 4px;
			resize: none;
			padding: 15px;
			user-select: none;
			@media screen and (max-width: 400px) {
				width: 250px; } }
		&-block {
			display: flex;
			flex-direction: column;
			align-items: center;
			margin-bottom: 42px; } }

	&__button {
		width: 120px;
		cursor: pointer;
		display: block;
		height: 30px;
		background: #FFFFFF;
		margin: 0 auto;
		border: 1px solid rgba(0, 0, 0, 0.49);
		box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.25);
		border-radius: 4px;
		transition: background 0.2s;
		&:hover {
			background: #eee; } } }
.thanks {
	margin-top: 114px;
	margin-bottom: 60px;
	margin: 0 auto;
	&__text {
		font-size: 36px;
		text-align: center;
		margin-bottom: 38px; }
	&__img {
		display: block;
		margin: 0 auto;
		width: 99px;
		height: 62px;
		margin-bottom: 130px; }
	&__button-img {
		padding: 5px 0px 5px 5px; } }
.error {
	border: 2px solid red; }



