.app {
	box-sizing: border-box {
    margin: 0; }
	padding: 0;
	outline: none;
	overflow: hidden; }

body {
    font-family: 'Merienda', cursive !important; }

.title {
    font-size: 24px;
    text-align: center;
    &-big {
        color: #ffffff;
        font-weight: bold;
        font-size: 40px;
        margin-bottom: 0;
        text-align: center;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); } }
.line {
    width: 240px;
    height: 1px;
    background-color: #000000;
    margin: 0 auto;
    margin-top: 60px;
    margin-bottom: 60px;
    @media screen and (max-width: 800px) {
        margin-bottom: 30px; } }

