.shop {
	&__item-img {
		display: block;
		max-width: 350px;
		margin: 0 auto;
		@media screen and (max-width: 400px) {
			max-width: 240px; } }
	&__read-more {
		cursor: pointer;
		font-weight: normal !important; }
	&__item-text {
		cursor: pointer;
		font-weight: normal !important; }
	&__item-full-text {
		font-weight: normal !important; }
	&__button-back {
		display: block;
		margin: 30px auto;
		cursor: pointer;
		display: block;
		padding: 10px;
		background: #FFFFFF;
		border: 1px solid rgba(0, 0, 0, 0.49);
		box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.25);
		border-radius: 4px;
		transition: background 0.2s;
		&:hover {
			background: #eee; } } }
.disabled {
	display: none; }
